import { Component, Inject, Input } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IconType } from '@core/enums';
import { Translations } from '@core/models';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-ad-unpublish-dialog',
    templateUrl: './my-ad-unpublish-dialog.component.html',
    styleUrls: ['./my-ad-unpublish-dialog.component.scss'],
    standalone: false
})
export class MyAdUnPublishDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data: any,
		public dialogRef: MatDialogRef<MyAdUnPublishDialogComponent>,
		private myAdsFacade: MyAdsFacade
	) {}

	readonly iconType = IconType;
	readonly buttonType = ButtonType;
	onCloseDialog(): void {
		this.dialogRef.close();
	}

	unpublishAd(): void {
		this.myAdsFacade.unpublishAd(this.data.adId, this.data.updatestatus, this.dialogRef, this.data.myAdHeaderStatusId);
	}
}
