import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IconType } from '@core/enums';
import { MyAdsFacade } from '@feature/my-ads/my-ads.facade';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-my-ad-mark-occupied-dialog',
    templateUrl: './update-status-dialog.component.html',
    styleUrls: ['./update-status-dialog.component.scss'],
    standalone: false
})
export class UpdateStatusDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data: any,
		public dialogRef: MatDialogRef<UpdateStatusDialogComponent>,
		private myAdsFacade: MyAdsFacade
	) {}

	readonly iconType = IconType;
	readonly buttonType = ButtonType;

	onCloseDialog(): void {
		this.dialogRef.close();
	}

	onUpdateStatus(): void {
		this.myAdsFacade.updateAdStatus(this.data.adId, this.data.status).subscribe(() => {
			this.dialogRef.close(true);
		});
	}
}
