import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { IconType } from '@core/enums';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-google-feedback-dialog',
    templateUrl: './google-feedback-dialog.component.html',
    styleUrls: ['./google-feedback-dialog.component.scss'],
    standalone: false
})
export class GoogleFeedbackDialogComponent {
	constructor(public dialogRef: MatDialogRef<GoogleFeedbackDialogComponent>) {}

	readonly iconType = IconType;
	readonly buttonType = ButtonType;

	onReviewUsOnGoogle(): void {
		window.open('https://www.google.com/search?q=findqo#lrd=0x8bd57843910f7fb9:0xccd9d9a51a0c9159,1,,,,', '_blank');

		this.dialogRef.close(true);
	}
}
